    padding-bottom: 60px; }
  @include r(375) {
    padding-bottom: 30px; }
  .container {
    overflow: hidden;
    h3.h3 {
      margin: 60px 0 80px;
      @include r(1024) {
        margin: 60px 0 60px; }
      @include r(375) {
        margin: 40px 0 40px; } }
    .overflow {
