.terms-page-wrapper {
  .breadcrambs-h1 {
    margin-bottom: 0; }
  .terms-wrapper {
    padding-top: 60px;
    padding-bottom: 80px;
    @include r(1024) {
      padding-bottom: 60px; }
    @include r(375) {
      padding-bottom: 60px;
      padding-top: 40px; }
    .subheader {
      margin-bottom: 40px;
      a {
        color: $green3;
        @include trans;
        &:hover {
          color: $gray2; } } }
    .content-wrapper {
      padding-left: 220px;
      padding-right: 220px;
      @include r(1024) {
        padding-left: 43px;
        padding-right: 127px; }
      @include r(768) {
        padding-left: 0;
        padding-right: 0; }
      ol {
        list-style-type: decimal;
        @include fs;
        font-size: 18px;
        line-height: 1.45;
        li {
          @include fs;
          font-size: 18px;
          line-height: 1.45;
          margin-bottom: 25px;
          @include r(1024) {
            font-size: 16px; }
          @include r(375) {
            font-size: 14px; }
          ol {
            margin-top: 30px;
            margin-left: 70px;
            @include r(1024) {
              margin-left: 40px; }
            @include r(768) {
              margin-left: 0; }
            @include r(375) {
              margin-top: 20px; }
            li {
              font-size: 18px;
              @include r(1024) {
                font-size: 16px; }
              @include r(375) {
                font-size: 14px; }
              @include fr;
              line-height: 1.45;
              color: $black2; } } } }
      ol {
        counter-reset: item;
        li {
          display: block; }
        li:before {
          content: counters(item, ".") ". ";
          counter-increment: item; } }
      p.more {
        @include fm;
        font-size: 14px;
        @include fm;
        line-height: 1.45;
        color: $gray2;
        span {
          color: $green3; } } } } }
