.about-us-wrapper {
  padding-bottom: 115px;
  background-color: $gray9;
  @include r(1024) {
    padding-bottom: 100px; }
  @include r(768) {
    padding-bottom: 90px; }
  .text-container {
    padding-left: 220px;
    padding-right: 330px;
    @include textdesktop;
    color: $black2;
    @include r(1024) {
      @include texttablet;
      padding-left: 0;
      padding-right: 210px; }
    @include r(768) {
      padding-right: 0; }
    @include r(375) {
      @include textmobile; } }
  ul {
    padding-left: 110px;
    @include r(1024) {
      padding-left: 84px; }
    @include r(768) {
      padding-left: 80px; }
    @include r(375) {
      padding-left: 40px; }
    li {
      @include maw(400); } } }
