.map-page-wrapper {
  .breadcrambs-h1 {
    margin-bottom: 0; }
  .map-wrapper {
    padding-top: 60px;
    padding-bottom: 80px;
    @include r(1024) {
      padding-bottom: 60px; }
    @include r(375) {
      padding-bottom: 60px;
      padding-top: 40px; }
    h3 {
      font-size: 20px;
      margin-bottom: 20px; }
    .content-wrapper {
      padding-left: 220px;
      padding-right: 220px;
      @include r(1024) {
        padding-left: 155px;
        padding-right: 155px; }
      @include r(768) {
        padding-left: 115px;
        padding-right: 115px; }
      @include r(620) {
        padding-left: 28px;
        padding-right: 0; } }
    ul {
      li {
        @include r(375) {
          margin-bottom: 20px; }
        &:after {
          display: none; }
        &:before {
          background-color: #000;
          height: 8px;
          width: 8px; }
        a {
          @include fr;
          font-size: 16px;
          color: $green3;
          line-height: 1.45;
          @include trans;
          @include r(375) {
            font-size: 14px; }
          &:hover {
            color: $gray2; } } }
      ul {
        margin-left: 25px;
        margin-top: 30px;
        @include r(375) {
          margin-top: 20px; }
        li {
          &:before {
            background-color: #ffffff;
            height: 8px;
            width: 8px;
            border: 1px solid #000000; } } }

      li.no {
        &:before {
          display: none; } } } } }
