.news-one-page-wrapper {
  .breadcrambs-h1 {
    margin-bottom: 0; }
  .one-news-wrapper {
    background-color: $gray9;
    padding-top: 60px;
    @include r(768) {
      padding-top: 50px; }
    .news-wrapper {
      display: flex;
      flex-direction: row;
      @include r(1024) {
        flex-direction: column; }
      .back {
        a {
          min-width: 290px;
          display: flex;
          align-items: center;
          text-transform: uppercase;
          @include fs;
          line-height: 1.45;
          font-size: 11px;
          color: $gray5;
          @include trans;
          @include r(1024) {
            margin-bottom: 40px;
            min-width: auto; }
          @include r(375) {
            margin-bottom: 34px; }
          .img {
            width: 30px;
            height: 30px;
            background-color: $green2;
            margin-right: 15px;
            border-radius: 50px;
            display: flex;
            position: relative;
            @include trans;
            &:before {
              position: absolute;
              top: 9px;
              left: 11px;
              content: url(../img/backtonews.svg); } }
          &:hover,
          &:active {
            color: $gray2;
            .img {
              background-color: $green1; } } } }
      .content {
        padding-right: 180px;
        @include r(1024) {
          padding-left: 83px;
          padding-right: 127px; }
        @include r(768) {
          padding-left: 0;
          padding-right: 0; }
        .date {
          @include fm;
          font-size: 18px;
          line-height: 1.45;
          color: $gray4;
          margin-bottom: 20px;
          @include r(1024) {
            font-size: 16px; }
          @include r(375) {
            font-size: 14px; } }
        h3.h3 {
          @include fm;
          font-size: 36px;
          line-height: 1.45;
          margin-top: 0;
          @include r(1024) {
            font-size: 24px; }
          @include r(375) {
            font-size: 16px; } }
        .text-wrapper {
          @include fr;
          font-size: 18px;
          line-height: 1.45;
          color: $black2;
          margin-bottom: 60px;
          @include r(1024) {
            font-size: 16px; }
          @include r(768) {
            margin-bottom: 40px; }
          @include r(375) {
            font-size: 14px;
            margin-bottom: 30px; } }
        .run-slider-wrapper {
          @include r(1024) {
            margin-bottom: 100px; }
          @include r(375) {
            margin-bottom: 60px; }
          .link-wrapper {
            padding-left: 0;
            padding-right: 0; }
          .runSlider {
            width: 100%; } } } } } }
